.confirm-container popEvent {
    .MuiPaper-root {
        background-color: initial;
        padding: 32px;
        width: 100%;
    }
}

.confirm_container {
    display: flex;
    height: 100%;
    justify-content: center;
    left: 0;
    position: fixed;
    top: 0;
    width: 100vw;
    z-index: 999;

    .body_container {
        background: #fff;
        border-radius: 12px 12px 0 0;
        bottom: -1px;
        color: #2c2c2c;
        font-size: 14px;
        line-height: 18px;
        max-height: 90vh;
        min-height: 195px;
        opacity: 1;
        padding: 24px;
        position: absolute;
        width: 100%;
        z-index: 999;

        .close_bar {
            align-items: center;
            background: transparent;
            display: flex;
            justify-content: center;
            position: absolute;
            top: 10px;
            width: calc(100% - 48px);
            z-index: 1000;

            .close_button {
                background: transparent;
                border: none;
                cursor: pointer;
                display: none;
                font-size: .86em;
                padding: initial;
            }

            .close_notch {
                background: #2c2c2c;
                border: none;
                border-radius: 5px;
                height: 4px;
                margin: auto;
                position: relative;
                width: 30px;
            }
        }
    }
}

.popEvent {
    .body_container {
        padding: 0 !important;

        .close_bar {
            margin-bottom: 16px;
            width: 100% !important;
        }
    }
}